header input {
    width: 8rem;
    height: 2rem;
    margin-bottom: 0.5rem;
    margin-top: 0.2rem;
    background-color: transparent;
    border: none;
    border-bottom-style: solid;
    border-bottom-color: #F8F8F8;
    color: #F8F8F8;
}

header input:focus, footer input:focus {
    outline: 0;
}

header label, header p {
    color: #F8F8F8;
    font-style: italic;
    margin-top: 1rem;
}

header, footer {
